p {
  font-size: 1.1em;
}

h2 {
  font-size: 1.6em;
}

h1 {
  margin-left: 3%;
  font-size: 2.5em;
}

.container {
  height: 86%;
  width: 86%;
  margin: 7%;
}

.content-container {
  display: grid;
  grid-template-columns: 1fr 4fr;
}

img {
  width: 15vw;
  height: 15vw;
  padding: 5%;
  border-radius: 50%;
  object-fit: cover;
}